.loading{
    width: 100vw;
    height: 100vh;
    background-color: white;
    display: grid;
    place-items: center;
    max-width: 100%;
}


.loading div {
    width:10vmax;
   
    height: 10vmax;
    border-bottom: 5px solid rgb(26, 24, 24);
    border-radius: 50%;
    animation: loadingRotate 800ms linear infinite;
}


@keyframes loadingRotate {
    to{
        transform: rotatez(-360deg);
    }
}